import React from 'react';
import { Form, Modal, Input, Alert } from 'antd';

import { ICloseOrderProps } from '@/types';
import styles from './styles.less';

const { Item: FormItem } = Form;
const { TextArea } = Input;

const CloseOrder = (props: ICloseOrderProps) => {
  const [ form ] = Form.useForm();
  const { visible, baseId, baseName, isLoading } = props;
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 }
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 17 }
    }
  };

  const handleOk = () => {
    form.validateFields().then(values => {
      props.onCloseOrder({
        ...values,
        goodsId: baseId
      }, () => handleCancel());
    }).catch(info => {
      console.log('Validate Failed:', info);
    });
  }

  const handleCancel = () => {
    form.resetFields();
    props.onCancel();
  }

  return (
    <Modal
      visible={visible}
      title=""
      onCancel={handleCancel}
      maskClosable={false}
      okType="danger"
      onOk={handleOk}
      confirmLoading={isLoading}
    >
      <Form
        {...formItemLayout}
        layout="horizontal"
        form={form}
      >
        <Alert
          message="!"
          description={<p className={styles.warnInfo}> <span>{baseName}</span> ?</p>}
          type="warning"
          showIcon
          style={{ marginBottom: 12 }}
        />
        
        <FormItem
          name="remarks"
          label=""
          rules={[{ required: true }]}
        >
          <TextArea placeholder='' />
        </FormItem>
      </Form>
    </Modal>
    
  )
}

export default CloseOrder;