import React from 'react';
import { Select } from 'antd';

import { errInfo } from './exception';
import { UserCardType } from './user';
import { GoodsState } from './goods';

const{ Option } = Select;

/**
 * @description:  map  lable
 * @param map 
 * @param value 
 * @return:  
 */
export const getLabel = (map, value, noneInfo = '') => map[value] || noneInfo;

/**
 * @description:  map  Option
 * @param map 
 * @return: <Option></Option>
 */
export const getOption = map => (
  Object.entries(map).map((state) => (
    <Option value={state[0]} key={state[0]}>{state[1]}</Option>
  ))
)

export {
  errInfo,
  UserCardType,
  GoodsState
}